<template>
  <div>
    <div id="mode" v-for="i in math">
      <div class="fs">
        <div class="top">
          <div class="img">
            <img :src="i.header" alt />
          </div>
          <span class="name">{{i.username}}</span>
          <span id="icon" class="glyphicon glyphicon-menu-down"></span>
        </div>
        <div>
          <p class="spack">{{i.text}}</p>
        </div>
      </div>
      <div class="wen">
        <div class="weng">
          <span id="fei" class="glyphicon glyphicon-send"></span>
          <p class="ha">{{i.top_commentsName}}</p>
        </div>
      </div>
      <div class="bottom">
        <div class="pinlun">
          <span class="glyphicon glyphicon-hand-up"></span>
          <span>{{i.comment}}</span>
        </div>
        <div class="pinlun">
          <span class="glyphicon glyphicon-hand-down"></span>
          <span>{{i.down}}</span>
        </div>
        <div class="pinlun">
          <span class="glyphicon glyphicon-calendar"></span>
          <span>{{i.forward}}</span>
        </div>
        <div class="pinlun">
          <span class="glyphicon glyphicon-share-alt"></span>
          <span>123</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      math: ""
    };
  },
  created() {
    this.$http
      .get("https://www.apiopen.top/satinGodApi?type=2&page=1", { params: {} })
      .then(data => {
        this.math = data.body.data;
        console.log(data);
      });
  }
};
</script>

<style scoped>
#mode {
  margin-bottom: 10px;
  background: white;
}
.fs {
  padding: 26px 28px 5px 28px;
}
.top {
  overflow: hidden;
  line-height: 65px;
}
.img {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  overflow: hidden;
  float: left;
}
.name {
  font-size: 27px;
  margin-left: 15px;
  float: left;
}
#icon {
  float: right;
  line-height: 65px;
  color: #cdcdcd;
}
.spack {
  font-size: 29px;
  padding: 10px 0;
  padding-top: 20px;
}
#video {
  max-height: 420px;
  margin-bottom: 20px;
}
.wen {
  padding: 0 28px;
}
.weng {
  display: inline-block;
  border-radius: 25px;
  background: #f5f1f2;
  height: 46px;
  line-height: 46px;
  padding: 2px 20px;
}
#fei {
  float: left;
  line-height: 46px;
  margin-right: 15px;
}
.ha {
  float: left;
}
.bottom {
  padding: 10px 28px;
  display: flex;
  text-align: center;
}
.pinlun {
  flex: 1;
}
</style>



