import Vue from 'vue'
import Router from 'vue-router'
import no1 from '@/components/no1.vue'
import no2 from '@/components/no2.vue'
import no3 from '@/components/no3.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: no1
    },
    {
      path: '/no2',
      component: no2
    },
    {
      path: '/no3',
      component: no3
    }
  ]
})



