<template>
  <div id="app">
    <!-- 头部 -->
    <div class="title">
      <span class="text" v-for="i in arr"><a :href="i.path">{{i.text}}</a></span>
      <span class="glyphicon glyphicon-search search" id="search"></span>
    </div>
    <!-- 内容 -->
    <div class="content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <!-- 底部 -->
    <div>

    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      arr:[
        {path:'#/',text:'推荐'},
        {path:'#/no2',text:'图片'},
        {path:'#/',text:'视频'},
        {path:'#/no3',text:'文字'}
      ]
    }
  }
};
</script>

<style>
body{
  background: #f7f7f7;
}
* {
  margin: 0;
  padding: 0;
}
.title{
  padding: 0 28px;
  line-height: 85px;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: white;
  z-index: 999;
}
.text{
  padding: 0 15px;
  font-size: 28px;
}
#search{
  font-size: 34px;
  float: right;
  line-height: 85px;
}
.content{
  margin-top: 85px;
  min-height: 500px;
}
</style>



